import React from "react";
import './index.css'

class Loading extends React.Component {
    constructor(props) {
        super(props)
        this.setState({
            isLoad:this.props.hide
        })
    }
    state={
        isLoad:true
    }
    componentWillReceiveProps(nextProps){
        if(this.state.isLoad != nextProps.hide){
            this.setState({
                isLoad:nextProps.hide
            })
        }
    }
    render() {
        return (
            <div id="loading" className={this.state.isLoad?'hide':''}>
                <div class="loading-liner ball-beat">
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
            </div>
        )
    }
}
export default Loading