import React from "react";
import { inject, observer } from 'mobx-react/index'

import {
    Drawer, Form, Input, Button, Rate, Checkbox, Row, Col, message, Spin
} from "antd";
import api from "../../../api/api";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'

@inject('appStore') @observer
class Role extends React.Component {
    constructor(props) {
        super(props)

    }
    state = {
        role: {
            id: '',
            value: ''
        },
        listPermission: [],
        rolePermission: [],
        AddRole: false,
        listType: [{ type: 1, value: '权限类' }, { type: 2, value: '栏目类' }, { type: 3, value: '文章类' }, { type: 4, value: '安全类' }, { type: 5, value: '通用类' }],
        initValue: []
    }
    componentWillMount() {
        NProgress.start()
    }
    componentWillUnmount() {
        NProgress.done()
    }
    componentDidMount() {
        if (!this.props.appStore.toleId) {
            this.props.appStore.setListRole()
        }
        this.props.appStore.setIsLoad(true)
        api('listPermission', 'post').then(res => {
            if (res.data.success) {
                let arr = []
                for (let index = 0; index < this.state.listType.length; index++) {
                    arr.push({ type: this.state.listType[index].type, value: this.state.listType[index].value, list: [] })
                    for (let i = 0; i < res.data.data.length; i++) {
                        const element = res.data.data[i];
                        if (this.state.listType[index].type == element.type) {
                            arr[index].list.push(element)
                        }
                    }
                }

                this.setState({
                    listPermission: arr
                })
                // this.getRole()

            }
        })
    }

    handleAddRole = (e) => {
        e.preventDefault();
        let validate = ''
        this.props.form.validateFields(['name', 'checkboxGroup'], (errors, values) => {
            // ...
            if (!validate && !errors) {
                validate = values
                // let userInfo=JSON.parse(isAuthenticated().split('?')[0])
                console.log([...new Set(values.checkboxGroup.join(''))])
                api('editRolePermission', 'post', {}, { 'permissionIds': [...new Set(values.checkboxGroup.join(''))], 'roleId': this.props.appStore.role.id }).then(res => {
                    if (res.data.success) {
                        api('getRolePermission', 'post', {}, {
                            roleId: this.state.role.id
                        }).then(res => {
                            message.success('修改成功')
                            console.log(res.data.data)
                            this.onCloseAddRole()
                            this.setState({
                                rolePermission: res.data.data
                            })
                            this.setState({
                                initValue: this.getRole(res.data.data)
                            })
                        })
                    }
                })
                // console.log(values)
            }

        });

    }
    onCloseAddRole = () => {
        this.setState({
            AddRole: false
        })

    }
    onOpenAddRole = () => {
        console.log(this.getRole(this.props.appStore.rolePermission))
        this.props.form.setFieldsValue({ 'checkboxGroup': this.getRole(this.props.appStore.rolePermission) })
        this.setState({
            AddRole: true
        })

    }
    getRole = (val) => {
        let arr = []
        if (val) {
            val.map(res => {
                if (res) {
                    arr.push(res.id)
                }

            })
            return arr
        }

    }
    getType = (val) => {

        return this.state.listType.map(res => {
            if (val == res.type) {
                return res.value
            }
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        {
            if (!this.props.appStore.isLoad) {
                return (<div id="role" ><Spin size="large"/></div>)
            } else {
                return (
                    <div id="role">
                        <div className="roleName">
                            {this.props.appStore.roleName.value}
                        </div>
                        <div className="title">
                            权限列表
                </div>
                        <div className="edit">
                            <span onClick={this.onOpenAddRole}>编辑</span>
                        </div>
                        <div className="listBox">
                            <div className="head flexBox">
                                <div>
                                    序列
                        </div>
                                <div>
                                    类型
                        </div>
                                <div>
                                    权限描述
                        </div>
                            </div>

                            {
                                this.props.appStore.rolePermission && this.props.appStore.rolePermission.slice().map((res, index) => {
                                    return (
                                        <div className="list flexBox">
                                            <div>
                                                {index + 1 >= 10 ? index + 1 : '0' + (index + 1)}
                                            </div>
                                            <div>
                                                {res && res.type ? this.getType(res.type) : ''}
                                            </div>
                                            <div>
                                                {res ? res.name : ''}
                                            </div>
                                        </div>
                                    )
                                })
                            }

                        </div>
                        <Drawer
                            title="编辑角色权限"
                            placement="right"
                            closable={false}
                            onClose={this.onCloseAddRole}
                            visible={this.state.AddRole}
                            width='38.53%'
                        >
                            <div className="drawer">
                                <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleAddDept}>
                                    <div className="drawer_title">
                                        <span>基础权限</span>
                                        <div className="flex"></div>

                                    </div>


                                    <div className='addRole'>
                                        <div className='head'>
                                            <span>选择权限</span>
                                            <div className="flex"></div>
                                        </div>



                                        <div className='formItem'>
                                            <Form.Item >
                                                {getFieldDecorator('checkboxGroup', { initialValue: this.state.initValue })(

                                                    <Checkbox.Group style={{ width: '100%' }} >
                                                        {
                                                            this.state.listPermission && this.state.listPermission.map(res => {
                                                                if (res && res.list && res.list.length > 0)
                                                                    return (
                                                                        <div>
                                                                            <div className="power_title">
                                                                                {
                                                                                    res.type == 1 ? '一. ' + res.value : res.type == 2 ? '二. ' + res.value : res.type == 3 ? '三. ' + res.value : res.type == 1 ? '四. ' + res.value : '五。 ' + res.value
                                                                                }
                                                                            </div>
                                                                            <Row>
                                                                                {
                                                                                    res.list.map(val => {
                                                                                        return (
                                                                                            <Col span={8}>
                                                                                                <Checkbox value={val.id}>{val.name}</Checkbox>
                                                                                            </Col>
                                                                                        )

                                                                                    })
                                                                                }


                                                                            </Row>
                                                                        </div>
                                                                    )
                                                            }

                                                            )
                                                        }
                                                    </Checkbox.Group>

                                                )}
                                            </Form.Item>
                                        </div>
                                    </div>
                                    <div className="btn">
                                        <Button className='save' onClick={this.handleAddRole}>保存</Button>

                                        <Button className='close' onClick={this.onCloseAddRole}>取消</Button>

                                    </div>
                                </Form>
                            </div>

                        </Drawer>

                    </div>

                )
            }

        }

    }
}
const WrappedRegistrationForm = Form.create()(Role);
export default WrappedRegistrationForm