import React from "react";
import { Breadcrumb, Icon, Table, Drawer, Form, Input, Select, Button, Checkbox, Modal, Menu, Collapse, message,Spin  } from 'antd';
import { Link } from 'react-router-dom';

import { isAuthenticated } from "../../../utils/Session";
import Loading from "../../../components/loading/loading";

import api from "../../../api/api";
import { inject, observer } from 'mobx-react/index'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'

const Search = Input.Search;
const Panel = Collapse.Panel;

const { Option } = Select;
const getSvgIcon = (style = {}, ) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    return (
        <span style={style}>
            <i >
                <svg
                    viewBox="0 0 1024 1024"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    style={{ verticalAlign: '-.125em' }}
                >
                    <path d={path} />
                </svg>
            </i>

        </span>
    );
};

function expandicon(props) {
    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '0px',
        color: '#B4B4B4',
        transform: `rotate(${props.isActive ? 90 : 0}deg)`,
        top: 0
    }, props);
}
@inject('appStore') @observer

class Dept extends React.Component {
    constructor(props) {
        super(props)



    }
    componentWillMount() {
        NProgress.start()
        this.setState({
            isLoad: false
        })
    }
    componentWillUnmount() {
        NProgress.done()

    }
    componentDidMount() {
        NProgress.done()
        this.setState({
            isLoad: true
        })
    }

    async listRole(val) {
        let res = await api('listRole', 'post', {}, { 'deptId': val })
        return res
    }
    componentWillReact() {
        // console.log(this.props.appStore)

    }

    componentDidUpdate() {


    }
    showAdd = () => {
        this.setState({
            drawerName: '添加员工'
        })
        let name = this.props.appStore.countDept.map(res => {
            if (res.deptId == this.props.appStore.seleteId) {
                return res.deptName
            }
        })
        this.props.appStore.setRoleList(this.props.appStore.seleteId)
        this.props.form.setFieldsValue({ 'dept': this.props.appStore.seleteId })
        this.listRole(this.props.appStore.seleteId).then(res => {
            if (res.data.success) {
                if (res.data.data.length > 0) {
                    this.props.form.setFieldsValue({ 'identity': res.data.data[0].id })
                    this.props.appStore.setRolePermission(res.data.data[0].id)
                    this.setState({
                        roleName: res.data.data[0].role
                    })
                }

            }
        })
        this.setState({
            add: true,
            deptList: [{ 'id': this.props.appStore.seleteId, 'name': name }, ...this.props.appStore.deptList]
        });
    };

    onCloseAdd = () => {
        console.log(234)
        this.props.form.resetFields()
        this.setState({
            add: false,
        });
    };
    showRevise = () => {
        this.setState({
            Revise: true,
        });
    };

    onCloseRevise = () => {
        this.props.form.resetFields()
        this.setState({
            Revise: false,
        });
    };
    showAddDept = () => {
        this.setState({
            addDept: true,
        });
    };

    onCloseAddDept = () => {
        this.props.form.resetFields()
        this.setState({
            addDept: false,
        });
    };
    showReviseUserInfo = (val) => {
        console.log(val)
        this.setState({
            userInfoId: val.id,
            userInfo: val,
            drawerName: '修改员工信息'

        })
        this.props.appStore.setRoleList(val.deptId)
        this.props.appStore.setRolePermission(val.roleId)

        this.props.form.setFieldsValue({ 'identity': val.roleId })
        this.listRole(val.deptId).then(res => {
            if (res.data.success) {
                if (res.data.data.length > 0) {


                    this.setState({
                        roleName: res.data.data[0].role
                    })
                }

            }
        })
        api('getBackUserPermission', 'post', {}, { 'backUserId': val.id }).then(res => {
            if (res.data.success) {
                console.log(res)

                this.setState({
                    userInfoList: this.getId(res.data.data),
                })
                console.log(this.props.appStore.rolePermission.slice().length, 1, res.data.data.length)
                if (this.props.appStore.rolePermission.slice().length == res.data.data.length) {
                    this.setState({
                        checked: true
                    })
                } else {
                    this.setState({
                        checked: false
                    })
                    this.props.form.setFieldsValue({ 'dept': val.deptId })
                    this.props.form.setFieldsValue({ 'other': this.getId(res.data.data), 'name': val.name, 'tel': val.telnum })
                }

            }
        })
        this.setState({
            add: true,
            deptList: [{ 'id': this.props.appStore.seleteId, 'name': this.props.appStore.deptName }, ...this.props.appStore.deptList]
        });
    }
    onCloseReviseUserInfo = (props) => {
        this.props.form.resetFields()
        this.setState({
            add: false,
        });
    }

    showReviseDept = () => {
        api('getDeptAdminMember', 'post', {}, { 'deptId': this.props.appStore.seleteId }).then(res => {
            if (res.data.success) {
                console.log(this.getId(res.data.adminPList))
                console.log(this.getId(res.data.memberPList))
                this.props.form.setFieldsValue({ 'admin2': this.getId(res.data.adminPList), 'member2': this.getId(res.data.memberPList) })
                this.setState({
                    memberPList: this.getId(res.data.memberPList),
                    adminPList: this.getId(res.data.adminPList),

                })
                this.setState({
                    ReviseDept: true,
                });
            }
        })

    };

    onCloseReviseDept = () => {
        this.props.form.resetFields()
        this.setState({
            ReviseDept: false,
        });
    };

    handleVisibleChange = () => {
        this.setState({
            visible: !this.state.visible
        })
    }
    state = {
        list: [],
        visible: false,
        columns: [
            {
                title: '姓名',
                dataIndex: 'name',
            },
            {
                title: '职位',
                dataIndex: 'roleName',
            },
            {
                title: '手机',
                dataIndex: 'telnum',
            },
            {
                title: '积分(剩余积分/累计积分)',
                dataIndex: 'points',
            },
            {
                title: '操作',
                dataIndex: '',
                render: (record) =>

                    <div className="ant-dropdown-link" onClick={() => this.showReviseUserInfo(record)} >
                        编辑
                        </div>

            },
        ],
        data: [],
        add: false,
        selectedItems: [],
        addDept: false,
        seleteIdLIst: [],
        seleteId: 0,
        // rowSelection object indicates the need for row selection\
        Breadcrumb: [],
        deptList: [],
        deptName: '',
        total: 0,
        listRole: '',
        listPermission: [],
        checked: true,
        ReviseDept: false,
        visibleModal: false,
        panelCheack: [],
        selectedRowKeys: [],
        selectedRowKey: [],
        ReviseDept: false,
        initValue: [],
        Revise: false,
        adminPList: [],
        memberPList: [],
        ReviseUserInfo: false,
        userInfoList: [],
        userInfoId: 0,
        isLoad: false,
        roleName: '',
        page: 1,
        drawerName: '',
        userInfo: '',
        secondId: 0,
        firstId: 0
    }
    onSelectChange = (selectedRowKeys, selectedRows) => {
        console.log('selectedRowKeys changed: ', selectedRowKeys, selectedRows);
        let arr = []
        selectedRows.map(res => {
            arr.push(res.id)
        })
        console.log(arr)
        this.setState({ selectedRowKeys: arr, selectedRowKey: selectedRowKeys });
    };
    handleSubmit = e => {
        e.preventDefault();
        let validate = ''
        this.props.form.validateFields(['name', 'tel'], (errors, values) => {
            // ...
            if (!errors) {
                validate = values
                console.log(values)
            }

        });
        if (validate) {
            let res = this.props.form.getFieldsValue(['identity', 'dept', 'other']);
            var arr = []
            if (this.state.checked) {

                this.props.appStore.rolePermission.slice().map(res => {
                    if (res) {
                        arr.push(res.id)
                    }
                })
            }
            api('saveEmployee', 'post', {}, {
                'name': validate.name,
                'telnum': validate.tel,
                'deptId': res.dept ? res.dept : this.props.appStore.seleteId,
                'roleId': res.identity ? res.identity : '',
                'listRoleId': this.state.checked ? arr.join(',') : res.other,
                adminId: JSON.parse(isAuthenticated().split('?')[0]).backUserId
            }).then(res => {
                this.props.form.resetFields()
                this.onCloseAdd()
                if (!res.data.errorMessage) {

                    message.success('创建成功')

                    this.props.appStore.setBackUserList()

                } else {
                    message.warning(res.data.errorMessage)
                }
            })
        }


    };
    saveRefresh = e => {
        e.preventDefault();
        let validate = ''
        this.props.form.validateFields(['name', 'tel'], (errors, values) => {
            // ...
            if (!errors) {
                validate = values
                // console.log(values)
            }

        });
        if (validate) {
            let res = this.props.form.getFieldsValue(['identity', 'dept', 'other']);
            if (this.state.checked) {
                var arr = []
                this.props.appStore.rolePermission.slice().map(res => {
                    if (res) {
                        arr.push(res.id)
                    }
                })
            }
            api('saveEmployee', 'post', {}, {
                'name': validate.name,
                'telnum': validate.tel,
                'deptId': res.dept ? res.dept : this.props.appStore.seleteId,
                'roleId': res.identity ? res.identity : '',
                'listRoleId': this.state.checked ? arr.join(',') : res.other,
                adminId: JSON.parse(isAuthenticated().split('?')[0]).backUserId

            }).then(res => {
                this.props.form.resetFields()
                if (!res.data.errorMessage) {

                    message.success('创建成功')

                    this.props.appStore.setBackUserList()

                } else {
                    message.warning(res.data.errorMessage)
                }
            })
        }
    }
    handleAddDept = e => {
        e.preventDefault();
        let validate = ''
        console.log(24)
        this.props.form.validateFields(['name1', 'admin1', 'member1'], (errors, values) => {
            // ...
            if (!errors && values) {
                console.log(values)
                validate = values
                api('saveDept', 'post', {
                    'name': validate.name1,
                    'adminPermissionIds': values.admin1 ? values.admin1 : '',
                    'parentId': this.props.appStore.seleteId,
                    'memberPermissionIds': values.member1 ? values.member1 : ''
                }).then(res => {
                    if (res.data.success) {
                        this.onCloseAddDept()
                        validate = ''
                        message.success('创建成功')

                        this.props.appStore.setDeptList()
                        this.props.appStore.setCountDept()

                    }
                })
                // console.log(values)
            } else {
                console.log(errors)
            }

        });

    }
    saveRefreshDept = e => {
        e.preventDefault();

        this.props.form.validateFields(['name1', 'admin1', 'member1'], (errors, values) => {
            // ...
            let validate = ''
            if (!errors && values) {
                validate = values
                api('saveDept', 'post', {
                    'name': validate.name1,
                    'adminPermissionIds': values.admin1 ? values.admin1 : '',
                    'parentId': this.props.appStore.seleteId,
                    'memberPermissionIds': values.member1 ? values.member1 : ''
                }).then(res => {
                    if (res.data.success) {
                        this.props.form.resetFields()
                        validate = ''
                        this.props.appStore.setDeptList()
                    }
                })
                // console.log(values)
            }

        });
    }

    handleRevise = (e) => {
        e.preventDefault();
        let validate = ''
        this.props.form.validateFields(['admin', 'member'], (errors, values) => {
            // ...
            console.log(values)
            if (!errors && !validate) {
                validate = values


                api('editDeptAdminMemberPer', 'post', {

                    'adminPermissionIds': values.admin.sort().toString() != this.state.adminPList.sort().toString() ? values.admin.join(',') : '',
                    'deptId': this.props.appStore.seleteId,
                    'memberPermissionIds': values.member.sort().toString() != this.state.memberPList.sort().toString() ? values.member.join(',') : ''
                }).then(res => {
                    if (res.data.success) {
                        this.onCloseAdd()
                        this.props.form.resetFields()
                        validate = ''
                        message.success('修改成功')
                    }
                })
                // console.log(values)
            }

        });
    }
    handleReviseUserInfo = (e) => {
        e.preventDefault();

        let validate = ''
        this.props.form.validateFields(['name', 'tel'], (errors, values) => {
            // ...
            if (!errors) {
                validate = values
                console.log(values)
            }

        });
        if (validate) {
            let val = this.props.form.getFieldsValue(['identity', 'dept', 'other']);
            var arr = []
            if (this.state.checked) {
                this.props.appStore.rolePermission.slice().map(res => {
                    if (res) {
                        arr.push(res.id)
                    }
                })
            }else{
                arr=val.other
            }
            api('editEmployee', 'post', {
                'name': validate.name,
                'telnum': validate.tel,
                'operatorId': JSON.parse(isAuthenticated().split('?')[0]).backUserId,
                'employeeId': this.state.userInfoId,
                'roleId':val.identity
            }).then(res => {
                if (!res.data.errorMessage) {
                    api('editUserPermission', 'post', {}, {
                        'listBackUserPer': arr.sort().toString() != this.state.userInfoList.sort().toString() ? arr.join(',') : '',
                        'backUserId': this.state.userInfoId,
                    }).then(res => {

                        this.props.form.resetFields()
                        this.onCloseAdd()
                        if (!res.data.errorMessage) {

                            message.success('创建成功')
                            this.onCloseReviseUserInfo()
                            validate = ''
                            this.props.appStore.setBackUserList()

                        } else {
                            message.warning(res.data.errorMessage)
                        }
                    })


                } else {
                    this.props.form.resetFields()
                    this.onCloseAdd()
                    message.warning(res.data.errorMessage)
                }
            })
        }

    }
    handleSelectChange = value => {
        console.log(value);
        this.props.appStore.setRoleList(value)
        console.log(this.props.appStore.roleList)
        // if (this.props.appStore.roleList.slice().length > 0) {
        //     this.props.form.setFieldsValue({ 'identity': this.props.appStore.roleList.slice()[0].id })

        // }
        this.listRole(value).then(res => {
            if (res.data.success) {
                if (res.data.data.length > 0) {
                    this.props.form.setFieldsValue({ 'identity': res.data.data[0].id })
                    this.props.appStore.setRolePermission(res.data.data[0].id)
                    this.props.form.setFieldsValue({ 'other': [] })

                    this.setState({
                        roleName: res.data.data[0].role
                    })
                }

            }
        })
    };
    handleSelectChangeRole = value => {
        console.log(value);
        this.props.appStore.setRolePermission(value)
        this.props.form.setFieldsValue({ 'other': [] })

        this.props.appStore.listRole.slice().map(res => {
            if (res.id == value) {
                this.setState({
                    roleName: res.role
                })
            }
        })
    }
    getRole = (val) => {
        let arr = []
        if (val) {
            val.map(res => {
                arr.push(res.id)
            })
            return arr
        }

    }

    onChange = (e) => {
        // console.log(`checked = ${e.target.checked}`);
        this.setState({
            checked: e.target.checked,
        });
    }
    handleChange = selectedItems => {
        this.setState({ selectedItems });
    };

    getCount = (val, val2) => {
        if (val2) {
            let res
            for (let index = 0; index < val2.length; index++) {
                const element = val2[index];
                if (element.deptId == val) {
                    res = `(${element.count}人)`
                }
            }
            // console.log(res)
            return res
        }

    }
    setPage = (val) => {
        console.log(val)
        let str = typeof val
        console.log(typeof val == 'number')

        if (str == 'number') {
            this.props.appStore.setBackUserList(val)
            this.setPage({
                page: val
            })
        }

    }
    visibleModalOk = () => {
        if (this.state.selectedRowKeys.length > 0) {
            this.setState({
                visibleModal: true
            })
        } else {
            message.warning('请选择员工！');
        }
    }
    visibleModalCancel = () => {
        this.setState({
            visibleModal: false,
            selectedRowKey: [],
            selectedRowKeys: []
        })
    }
    onPanel = (val) => {
        console.log(val)
    }
    sendAdjust = () => {
        if (this.state.panelCheack.length > 0) {
            // console.log(this.state.selectedRowKeys)
            api('moveDept', 'post', {}, {
                'deptId': this.state.panelCheack[0],
                'listUserId': this.state.selectedRowKeys.join(',')
            }).then(res => {
                if (res.data.success) {
                    this.visibleModalCancel()
                    this.deletePanelCheack()
                    this.setState({ selectedRowKeys: [], selectedRowKey: [] });
                    this.props.appStore.setDeptList()
                    this.props.appStore.setBackUserList(1)

                }
            })
        } else {
            message.warning('请选择要调整的部门！');
        }
    }
    onPanelCheack = (e) => {
        // console.log(e)
        this.setState({
            panelCheack: e
        })
    }
    deletePanelCheack = () => {
        this.setState({
            secondId: 0
        })
    }
    getId = (val) => {
        let arr = []
        console.log(val)
        if (val) {
            val.map(res => {
                if (res)
                    arr.push(res.id)
            })
            return arr
        }

    }
    setDept = (val) => {
        console.log(val)
        // console.log(this.getUserId(val.linked))

        // console.log(this.props.appStore.seleteIdLIst.indexOf(val))
        this.props.appStore.setIdList(this.getUserId(val.linked))
        this.props.appStore.setDeptId(val.id)
        this.props.appStore.setopenkeys(this.setStr(this.getUserId(val.linked)))
        this.props.appStore.setIsLoad(false)
        setTimeout(() => {
            this.props.appStore.setIsLoad(true)
        }, 300)
    }
    getUserId = (val) => {
        let arr = []
        if (val) {
            val.split('|').map(res => {
                if (res) {
                    arr.push(res)
                }
            })
        }
        return arr
    }
    setStr(val) {
        console.log(val)
        let arr = []
        if (val) {
            let str = '|'
            val.map(res => {
                str += res + '|'
                arr.push(str)
            })

        }
        return arr
    }
    deleteUser = () => {
        console.log(JSON.parse(isAuthenticated().split('?')[0]).backUserId)

        if (this.state.selectedRowKeys.length < 1) {
            message.warning('请选择员工！');
        } else {
            api('banBackUser', 'post', {}, {
                'adminId': JSON.parse(isAuthenticated().split('?')[0]).backUserId,
                'empIds': this.state.selectedRowKeys.join(',')
            }).then(res => {
                if (res.data.success) {
                    message.success('删除成功')
                    this.setState({
                        selectedRowKey: []
                    })
                    this.props.appStore.setDeptList()
                    this.props.appStore.setBackUserList(this.state.page)
                } else {
                    this.setState({
                        selectedRowKey: []
                    })
                    message.warning(res.data.errorMessage);
                }
            })
        }

    }
    onChangeSecond = (val) => {
        this.setState({
            secondId: val
        })

    }
    onChangeFirst = (val) => {
        console.log(val)
        this.setState({
            secondId: val
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        // console.log(total)
        if (!this.props.appStore.isLoad) {
                return (<div id="contentDept" ><Spin size="large"/></div>)
            } else {
        return (
            <div id="contentDept">
                <div >
                    <div className="title">
                        {this.props.appStore.deptName}
                        <span className="setting" onClick={this.showReviseDept}>
                            设置
                </span>
                    </div>
                    <div className="sparator">
                        <Breadcrumb separator=">">
                            {
                                this.props.appStore.breadcrumb && this.props.appStore.breadcrumb.slice().map(res => {
                                    return (<Breadcrumb.Item onClick={() => this.setDept(res)}>
                                        <div ClassName={this.props.appStore.seleteId == res.id ? 'link-active' : ''}>
                                            {res.name}
                                        </div>
                                    </Breadcrumb.Item>)
                                })
                            }
                        </Breadcrumb>
                    </div>
                    <div className="dept" onClick={this.showAddDept}>
                        <img className='firstImg' src={require("./../../../assets/images/img-staff-3.png")} />
                        下级部门
                    </div>
                    {
                        this.props.appStore.seleteIdLIst.length >= 3 ? '' : <div className="dept_adit">
                            <span onClick={this.showAddDept}>添加子部门</span>
                        </div>
                    }

                    <div className="dept_list">
                        {
                            this.props.appStore.deptList == '' ? (
                                <div className="noDept">
                                    当前部门不包含下级部
                                   {
                                        this.props.appStore.seleteIdLIst.length >= 3 ? '' : <span onClick={this.showAddDept}>添加子部门</span>
                                    }
                                </div>
                            ) : this.props.appStore.deptList.slice() && this.props.appStore.deptList.slice().map(res => {
                                return (
                                    <div className="list" onClick={() => this.setDept(res)}>
                                        <p>{res.name}{this.getCount(res.id, this.props.appStore.countDept.slice())}</p>
                                        <Icon type='right'></Icon>
                                    </div>
                                )
                            })
                        }

                    </div>
                    <div className="staff">
                        <div className="staff_title">
                            <img className='firstImg' src={require("./../../../assets/images/img-staff-1.png")} />

                            部门员工
                    </div>
                        <div className="staff_operation">
                            <span className='add' onClick={this.showAdd}>添加成员</span>
                            <span className='add_all'>批量导入员工</span>
                            <span className='adjust' onClick={this.visibleModalOk}>调整部门</span>
                            <span className='delete' onClick={this.deleteUser}>批量删除</span>
                        </div>
                        <Table rowSelection={{ onChange: this.onSelectChange, selectedRowKeys: this.state.selectedRowKey }} columns={this.state.columns} dataSource={this.props.appStore.backUserList.slice()} pagination={{ total: Number(this.props.appStore.backUserListTotal), onChange: this.setPage, 'pageSize': 10 }} />

                    </div>
                    <Drawer
                        title={this.state.drawerName}
                        placement="right"
                        closable={false}
                        onClose={this.onCloseAdd}
                        visible={this.state.add}
                        width='38.53%'
                    >
                        <div className="drawer">
                            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                                <div className="drawer_title">
                                    <span>基础信息</span>
                                    <div className="flex"></div>

                                </div>

                                <Form.Item label="姓名:">
                                    {getFieldDecorator('name', {
                                        rules: [{ required: true, message: "姓名不能为空！", }],
                                    })(<Input autocomplete="off" />)}
                                </Form.Item>
                                <Form.Item label="手机:">
                                    {getFieldDecorator('tel', {
                                        rules: [{ required: true, enum: 'number', len: 11, message: "手机号不正确！", }],
                                    })(<Input type='tel' autocomplete="off" />)}
                                </Form.Item>

                                <Form.Item label="部门:">
                                    {getFieldDecorator('dept', {
                                        rules: [{ required: false, help: '', type: 'number' }],
                                    })(
                                        <Select
                                            disabled={this.state.drawerName=='修改员工信息'}
                                            onChange={this.handleSelectChange}
                                        >
                                            {/* <Option key={this.props.appStore.deptId} value={this.props.appStore.deptId}>{this.props.appStore.deptName}</Option> */}
                                            {
                                                this.state.deptList && this.state.deptList.slice().map(res => {
                                                    return (
                                                        <Option value={res.id} key={res.id}>{res.name}</Option>
                                                    )
                                                })
                                            }
                                        </Select>,
                                    )}
                                </Form.Item>
                                <Form.Item label="身份:">
                                    {getFieldDecorator('identity', {
                                        rules: [{ required: false, help: '', type: 'number', initialValue: this.props.appStore.roleId }],
                                    })(
                                        <Select

                                            onChange={this.handleSelectChangeRole}
                                        >
                                            {
                                                this.props.appStore.roleList.slice() && this.props.appStore.roleList.slice().map(res => {
                                                    return (
                                                        <Option key={res.id} value={res.id}>{res.role}</Option>
                                                    )
                                                })
                                            }
                                        </Select>,
                                    )}
                                </Form.Item>
                                <div className='power'>
                                    <div className='head'>
                                        <span>权限</span>
                                        <div className="flex"></div>
                                    </div>
                                    <div className="ckeckBox">
                                        <div ><Checkbox checked={this.state.checked} onChange={this.onChange}>基础权限:</Checkbox></div>
                                        <p>
                                            <span>{this.state.roleName}权限:</span>{this.props.appStore.rolePermission.slice() && this.props.appStore.rolePermission.slice().map(res => {
                                                return res ? res.name + ',' : ''
                                            })}
                                        </p>

                                    </div>
                                </div>
                                <Form.Item label="其他权限:">
                                    {getFieldDecorator('other', {
                                        rules: [{ required: false, help: '', type: 'array' }],
                                    })(
                                        <Select
                                            mode="multiple"


                                            onChange={this.handleChange}
                                            style={{ width: '100%' }}
                                            showArrow={true}
                                        >
                                            {this.props.appStore.rolePermission.slice() && this.props.appStore.rolePermission.slice().filter(o => !this.state.selectedItems.includes(o)).map(item => {
                                                if (item) {
                                                    return (
                                                        <Select.Option key={item.id} value={item.id}>
                                                            {item.name}
                                                        </Select.Option>
                                                    )
                                                }
                                            })}
                                        </Select>
                                    )}
                                </Form.Item>
                                {this.state.drawerName == '添加成员' ? <div className="btn">
                                    <Button className='save' onClick={this.handleSubmit}>保存</Button>
                                    <Button className='saveRefresh' onClick={this.saveRefresh}>保存并继续添加</Button>
                                    <Button className='close' onClick={this.onCloseAdd}>取消</Button>

                                </div> : <div className="btn">
                                        <Button className='save' onClick={this.handleReviseUserInfo}>保存</Button>
                                        <Button className='close' onClick={this.onCloseReviseUserInfo}>取消</Button>

                                    </div>}

                            </Form>

                        </div>

                    </Drawer>
                    <Drawer
                        title="添加部门"
                        placement="right"
                        closable={false}
                        onClose={this.onCloseAddDept}
                        visible={this.state.addDept}
                        width='38.53%'
                    >
                        <div className="drawer">
                            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleAddDept}>
                                <div className="drawer_title">
                                    <span>基础信息</span>
                                    <div className="flex"></div>

                                </div>

                                <Form.Item label="部门名称:">
                                    {getFieldDecorator('name1', {
                                        rules: [{ required: true, message: "部门名称不能为空！" }],
                                    })(<Input autocomplete="off" />)}
                                </Form.Item>


                                <div className='power'>
                                    <div className='head'>
                                        <span>权限</span>
                                        <div className="flex"></div>
                                    </div>

                                </div>
                                <Form.Item label="管理员:">
                                    {getFieldDecorator('admin1', {
                                        rules: [{ required: false, type: 'array' }],
                                    })(
                                        <Select
                                            mode="multiple"


                                            style={{ width: '100%' }}
                                            showArrow={true}

                                        >
                                            {this.props.appStore.listPermission && this.props.appStore.listPermission.slice().filter(o => !this.state.selectedItems.includes(o)).map(item => (
                                                <Select.Option key={item.id} value={item.id}>
                                                    {item.name}
                                                </Select.Option>
                                            ))}
                                        </Select>
                                    )}
                                </Form.Item>
                                <Form.Item label="成员:">
                                    {getFieldDecorator('member1', {
                                        rules: [{ required: false, type: 'array' }],
                                    })(
                                        <Select
                                            mode="multiple"

                                            style={{ width: '100%' }}
                                            showArrow={true}

                                        >
                                            {this.props.appStore.listPermission && this.props.appStore.listPermission.slice().filter(o => !this.state.selectedItems.includes(o)).map(item => (
                                                <Select.Option key={item.id} value={item.id}>
                                                    {item.name}
                                                </Select.Option>
                                            ))}
                                        </Select>
                                    )}
                                </Form.Item> <div className="btn">
                                    <Button className='save' onClick={this.handleAddDept}>保存</Button>
                                    <Button className='saveRefresh' onClick={this.saveRefreshDept}>保存并继续添加</Button>
                                    <Button className='close' onClick={this.onCloseAddDept}>取消</Button>

                                </div>

                            </Form>
                        </div>

                    </Drawer>
                    <Drawer
                        title="修改部门权限"
                        placement="right"
                        closable={false}
                        onClose={this.onCloseReviseDept}
                        visible={this.state.ReviseDept}
                        width='38.53%'
                    >
                        <div className="drawer">
                            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} >



                                <div className='power'>
                                    <div className='head'>
                                        <span>权限</span>
                                        <div className="flex"></div>
                                    </div>

                                </div>
                                <Form.Item label="管理员:">
                                    {getFieldDecorator('admin2', {
                                        rules: [{ required: false, type: 'array', initialValue: this.state.adminPList }],
                                    })(
                                        <Select
                                            mode="multiple"


                                            style={{ width: '100%' }}
                                            showArrow={true}

                                        >
                                            {this.props.appStore.listPermission && this.props.appStore.listPermission.filter(o => !this.state.selectedItems.includes(o)).map(item => (
                                                <Select.Option key={item.id} value={item.id}>
                                                    {item.name}
                                                </Select.Option>
                                            ))}
                                        </Select>
                                    )}
                                </Form.Item>
                                <Form.Item label="成员:">
                                    {getFieldDecorator('member2', {
                                        rules: [{ required: false, type: 'array', initialValue: this.state.memberPList }],
                                    })(
                                        <Select
                                            mode="multiple"
                                            style={{ width: '100%' }}
                                            showArrow={true}
                                        >
                                            {this.props.appStore.listPermission && this.props.appStore.listPermission.filter(o => !this.state.selectedItems.includes(o)).map(item => (
                                                <Select.Option key={item.id} value={item.id}>
                                                    {item.name}
                                                </Select.Option>
                                            ))}
                                        </Select>
                                    )}
                                </Form.Item>
                                <div className="btn">
                                    <Button className='save' onClick={this.handleRevise}>保存</Button>
                                    <Button className='close' onClick={this.onCloseReviseDept}>取消</Button>

                                </div>
                            </Form>
                        </div>

                    </Drawer>
                    <Drawer
                        title="修改成员权限"
                        placement="right"
                        closable={false}
                        onClose={this.onCloseReviseUserInfo}
                        visible={this.state.ReviseUserInfo}
                        width='38.53%'
                    >
                        <div className="drawer">
                            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>

                                <div className='power'>
                                    <div className='head'>
                                        <span>权限</span>
                                        <div className="flex"></div>
                                    </div>
                                    <div className="ckeckBox">
                                        <div ><Checkbox checked={this.state.checked} onChange={this.onChange}>基础权限:</Checkbox></div>
                                        <p>
                                            <span>成员权限:</span>{this.props.appStore.rolePermission.slice() && this.props.appStore.rolePermission.slice().map(res => {
                                                if (res) {
                                                    return res.name + ','
                                                }
                                            })}
                                        </p>

                                    </div>
                                </div>
                                <Form.Item label="其他权限:">
                                    {getFieldDecorator('other', {
                                        rules: [{ required: false, help: '', type: 'array', initialValue: this.state.userInfoList }],
                                    })(
                                        <Select
                                            mode="multiple"


                                            style={{ width: '100%' }}
                                            showArrow={true}

                                        >
                                            {this.props.appStore.rolePermission.slice() && this.props.appStore.rolePermission.slice().filter(o => !this.state.selectedItems.includes(o)).map(item => {
                                                if (item) {
                                                    return (
                                                        <Select.Option key={item.id} value={item.id}>
                                                            {item.name}
                                                        </Select.Option>
                                                    )
                                                }
                                            })}
                                        </Select>
                                    )}
                                </Form.Item>

                                <div className="btn">
                                    <Button className='save' onClick={this.handleReviseUserInfo}>保存</Button>
                                    <Button className='close' onClick={this.onCloseReviseUserInfo}>取消</Button>

                                </div>
                            </Form>

                        </div>

                    </Drawer>

                    <Modal
                        title="选择部门"
                        visible={this.state.visibleModal}
                        onOk={this.visibleModalOk}
                        onCancel={this.visibleModalCancel}
                        footer={null}
                        closable={false}
                        centered={true}
                        width='51.56%'
                        destroyOnClose={true}
                        wrapClassName="adjustment"
                    >

                        <div className="container">
                            <div className="flex select">
                                <div className="title">选择：</div>
                                <div className="content">
                                    <div className="search">
                                        <Search
                                            placeholder="搜索"
                                            onSearch={this.getSearch}
                                            style={{ width: '91.63%' }}
                                        />
                                    </div>
                                    <div className="tips">通讯录</div>

                                    <Collapse accordion='true' expandIcon={expandicon} bordered={false} showArrow={false} onChange={this.onCollapseChange}>
                                        {
                                            this.props.appStore.getCompanyBuild && this.props.appStore.getCompanyBuild.list && this.props.appStore.getCompanyBuild.list.map((res, index) => {
                                                if (res && res.list && res.list.length > 0) {
                                                    return (
                                                        <Panel header={<span className='panelHead'>
                                                            <img src={require("./../../../assets/images/img-staff-2.png")} style={StyleSheet.img} />
                                                            <span>{res.name}</span>
                                                        </span>} key={res.id} >
                                                            <div >
                                                                {res.list && res.list.map((res, i) => {
                                                                    if (res && res.name) {
                                                                        return (<span className='second' onClick={() => {
                                                                            this.onChangeSecond(res.id)
                                                                        }} key={res.id}>{
                                                                                res.id == this.state.secondId ? <Icon type="check-square" theme="filled" /> : <span className='icon'></span>
                                                                            }{res.name}</span>)
                                                                    }


                                                                })}
                                                            </div>
                                                        </Panel>
                                                    )
                                                } else {
                                                    return (<div className='firstColumn' key={res.id}>
                                                        <span className='second' onClick={(e) => {
                                                            this.onChangeFirst(res.id)
                                                        }}>{
                                                                res.id == this.state.secondId ? <Icon type="check-square" theme="filled" /> : <span className='icon'></span>
                                                            }{res.name}</span>
                                                    </div>)
                                                }

                                            })
                                        }

                                    </Collapse>
                                </div>
                            </div>
                            <div className="flex selected">
                                <div className="title">已选：</div>
                                <div className="content">
                                    {
                                        this.state.secondId != 0 ? <div className="selectedList">
                                            <img src={require("./../../../assets/images/img-staff-2.png")} alt="" style={StyleSheet.imgCheck} />
                                            {this.props.appStore.countDept.slice() && this.props.appStore.countDept.slice().map(res => {
                                                if (res.deptId == this.state.secondId) {
                                                    return res.deptName
                                                }
                                            })}
                                            <Icon type="close-circle" style={StyleSheet.iconstyle} onClick={this.deletePanelCheack} />
                                        </div> : ''
                                    }
                                </div>

                            </div>

                        </div>
                        <div className="btn">
                            <div className="send" onClick={this.sendAdjust}>确认</div>
                            <div className="close" onClick={this.visibleModalCancel}>取消</div>
                        </div>
                    </Modal>

                </div>
            </div>
        )
        }
    }
}
const StyleSheet = {
    img: {
        width: '18px',
        verticalAlign: 'middle',
        marginTop: '-1.2%',
        marginRight: '8px',

    },
    imgCheck: {
        width: '18px',
        verticalAlign: 'middle',
        marginTop: '-1.2%',
        marginRight: '8px',
        marginLeft: '20px',

    },
    iconstyle: {
        position: 'absolute',
        right: 0,
        top: '50%',
        transform: 'translateY(-50%)',
        cursor: 'pointer'
    },
    hide: {
        'display': 'none'
    }
}
const WrappedRegistrationForm = Form.create()(Dept);
export default WrappedRegistrationForm