import React from 'react'
import { BrowserRouter as Router, Route, Link, NavLink, Switch } from 'react-router-dom';

import { Input, Tabs, Menu, Drawer, Form, Button, Icon, Checkbox, Row, Col, message } from 'antd';
import { inject, observer } from 'mobx-react/index'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import api from "../../../api/api";
import Dept from "../Dept/Dept";
import Role from "../Role/Role";
import './index.css'

const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
const MenuItem = Menu.Item;
const CheckboxGroup = Checkbox.Group;
const Search = Input.Search;


const getSvgIcon = (style = {}, text) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    const path1 = 'M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z'
    return (
        <i style={style}>
            <svg
                viewBox="0 0 1024 1024"
                width="1em"
                height="1em"
                fill="currentColor"
                style={{ verticalAlign: '-.125em' }}
            >
                <path d={path} />
            </svg>
        </i>
    );
};
function itemIcon(props) {
    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '10px',
    });
}
function expandIcon(props) {

    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '10px',
        transform: `rotate(${props.isOpen ? 90 : 0}deg)`,

    });
}


@inject('appStore', 'headerStore') @observer

class Index extends React.Component {
    constructor(props) {
        super(props);
        let arr = this.props.location.pathname.split('/')
        this.props.headerStore.setCurrunt(this.props.location.pathname)

        if (arr[arr.length - 1].length == 1) {
            console.log(this.props.appStore)
            this.props.history.push('/Staff/Dept')
        }

        //监听子组件变化
    }
    state = {
        openKeys: ['|1|'],
        secondKey: '',
        list: '',
        countDept: '',
        tabKey: 1, seleteId: ['1'], seleteIdLIst: [1],
        collapsed: false,
        roleList: [],
        roleId: ['0'],
        roleName: '',
        AddRole: false,
        listType: [{ type: 1, value: '权限类' }, { type: 2, value: '栏目类' }, { type: 3, value: '文章类' }, { type: 4, value: '安全类' }, { type: 5, value: '通用类' }],
        Dept: '',
        Role: '',
        deptIdList: [],
        listPermission: [],
        
    };
    getSearch = (val) => {
        console.log(val)
    }
    callback = (key) => {
        // console.log(key);
        this.setState({
            tabKey: key
        })
        if (key == 2) {
            this.props.appStore.setIsLoad(false)

            this.props.appStore.setListRole()
            this.props.appStore.setRolePermission(this.props.appStore.roleId)
            setTimeout(() => {
                this.props.appStore.setIsLoad(true)
            }, 200)
            // this.props.history.push('/Article/Role')
        } else {
            // this.props.history.push('/Article/Dept')
        }
        // this.state.tabKey = key
    }

    async listRole() {
        let res = await api('listRole', 'post', {}, { 'deptId': '' })
        return res
    }
    componentWillMount() {

        this.props.appStore.setCompanyBuild()
        this.props.appStore.setCountDept()
        this.props.appStore.setListPermission()
        if (this.props.location.pathname != '/Staff/Role') {
            console.log(this.props.location.pathname)

            this.setState({
                tabKey: 1
            })
        } else {
            console.log(this.props.location.pathname)
            this.props.appStore.setRolePermission(this.props.appStore.roleId)
            this.setState({
                tabKey: 2
            })
        }

    }
    componentDidMount() {
        api('listPermission', 'post').then(res => {
            if (res.data.success) {
                let arr = []
                for (let index = 0; index < this.state.listType.length; index++) {
                    arr.push({ type: this.state.listType[index].type, value: this.state.listType[index].value, list: [] })
                    for (let i = 0; i < res.data.data.length; i++) {
                        const element = res.data.data[i];
                        if (this.state.listType[index].type == element.type) {
                            arr[index].list.push(element)
                        }
                    }
                }

                this.setState({
                    listPermission: arr
                })
                // this.getRole()

            }
        })
    }
    componentWillUnmount() {
        NProgress.done()
    }
    componentWillUpdata(nextProps, nextState) {
        console.log(nextState)
    }

    onOpenChange = openKeys => {
        console.log(this.props.appStore.seleteId)
        const latestOpenKey = openKeys.find(key => this.props.appStore.openkeys.slice().indexOf(key) === -1);
        // console.log(openKeys.includes(this.state.list.id.toString()),this.state.list.id.toString())
        if (!openKeys.includes(this.props.appStore.getCompanyBuild.linked)) {
            openKeys = [this.props.appStore.getCompanyBuild.linked]
        }
        let arr = this.props.appStore.openkeys.slice()

        if (latestOpenKey && latestOpenKey.length == 5) {
            arr[1] = latestOpenKey
        } else if (latestOpenKey && latestOpenKey.length == 7) {
            arr[2] = latestOpenKey
        } else {
            arr = openKeys
        }

        this.props.appStore.setopenkeys(arr)
        // if (this.props.appStore.deptIdList.indexOf(latestOpenKey) === -1) {
        //     this.props.appStore.setopenkeys(openKeys)
        // } else {
        //     // console.log(latestOpenKey)

        //     this.props.appStore.setopenkeys(latestOpenKey ? [latestOpenKey] : [])
        // }

    }
    onDeselect = val => {
        console.log(val)
    }
    onCloseAddRole = () => {
        this.setState({
            AddRole: false
        });
    }
    onShowAddRole = () => {
        console.log(1234)
        this.setState({
            AddRole: true
        });
    }
    getCount = (val, val2) => {
        if (val2) {
            let res
            for (let index = 0; index < val2.length; index++) {
                const element = val2[index];
                if (element.deptId == val) {
                    res = `(${element.count}人)`
                }
            }
            // console.log(res)
            return res
        }

    }
    selete = val => {
        console.log(val)
        if (val.key.length == 5) {
            let arr = [this.props.appStore.openkeys.slice()[0]]
            arr.push(val.key)
            this.props.appStore.setopenkeys(arr)
        }
        if (this.getUserId(val.key)[this.getUserId(val.key).length - 1] != this.props.appStore.seleteId) {
            this.props.appStore.setDeptId(this.getUserId(val.key)[this.getUserId(val.key).length - 1])
            this.props.appStore.setIdList(this.getUserId(val.key))
            this.props.appStore.setIsLoad(false)
            setTimeout(() => {
                this.props.appStore.setIsLoad(true)
            }, 300)
        }
    }

    onTitleClick = val => {
        console.log(this.getUserId(val.key)[this.getUserId(val.key).length - 1])
        console.log(this.props.appStore.seleteId)
        // this.props.history.push(`/Staff/Dept/${this.getUserId(val.key)[this.getUserId(val.key).length - 1]}`)
        if (this.getUserId(val.key)[this.getUserId(val.key).length - 1] != this.props.appStore.seleteId) {
            this.props.appStore.setDeptId(this.getUserId(val.key)[this.getUserId(val.key).length - 1])
            this.props.appStore.setIdList(this.getUserId(val.key))
            this.props.appStore.setIsLoad(false)
            setTimeout(() => {
                this.props.appStore.setIsLoad(true)
            }, 300)
        }


    }
    handleClick = val => {
        console.log(val.item.props.value)
        // this.setState({
        //     roleName: {
        //         value: val.item.props.value,
        //         id: val.key
        //     }
        // })
        let obj = {
            value: val.item.props.value,
            id: val.key
        }
        this.props.appStore.setIsLoad(false)

        this.props.appStore.setRoleId(val.key)
        this.props.appStore.setRoleName(obj)
        // setTimeout(() => {
        //     this.props.appStore.setIsLoad(true)
        // }, 300)
    }
    getUserId = (val) => {
        let arr = []
        if (val) {
            val.split('|').map(res => {
                if (res) {
                    arr.push(res)
                }
            })
        }
        return arr
    }


    handleAddRole = (e) => {
        e.preventDefault();
        let validate = ''
        this.props.form.validateFields(['name', 'checkboxGroup'], (errors, values) => {
            // ...
            if (!validate && !errors) {
                validate = values
                api('saveRole', 'post', {
                    "roleName": values.name,
                    "permissionIds": values.checkboxGroup
                }).then(res => {
                    if (res.data.success) {
                        this.props.form.resetFields()
                        // this.propsChange()
                        this.onCloseAddRole()
                        validate = ''
                        this.props.appStore.setRoleList()
                    }
                })
                // console.log(values)
            }

        });
    }
    saveRefreshRole = (e) => {
        e.preventDefault();
        let validate = ''
        this.props.form.validateFields(['name', 'checkboxGroup'], (errors, values) => {
            // ...
            if (!validate && !errors) {
                validate = values
                // console.log(values)
                api('saveRole', 'post', {
                    "roleName": values.name,
                    "permissionIds": values.checkboxGroup
                }).then(res => {
                    if (res.data.success) {
                        this.props.form.resetFields()
                        // this.propsChange()
                        message.success('创建成功')

                        validate = ''
                        this.props.appStore.setRoleList()

                    }
                })
                // console.log(values)
            }

        });

    }

    setStr(val) {
        console.log(val)
        let arr = []
        if (val) {
            let str = '|'
            val.map(res => {
                str += res + '|'
                arr.push(str)
            })

        }
        return arr
    }
    setMebuItem = (val) => {

        return (
            <Menu.Item key={val.linked} className='thirdDept' style={Styles.submenu}><div className={this.props.appStore.seleteId==val.id?'link-active a':'a'} activeClassName='link-active'>{val.name}{this.getCount(val.id, this.props.appStore.countDept)}</div></Menu.Item>
        )

    }
    setSubMenu = (val) => {
        return (
            <SubMenu key={val.linked} title={<div  className={this.props.appStore.seleteId==val.id?'link-active a':'a'}>
                <Icon type="caret-right" />
                <img className='otherImg' src={require("./../../../assets/images/img-staff-2.png")} />
                <span>{val.name}{this.getCount(val.id, this.props.appStore.countDept)}</span>
            </div>}
                style={Styles.submenu}
                onTitleClick={this.onTitleClick}
            >
                {
                    val.list.map(res => {
                        return res.list ? this.setSubMenu(res) : this.setMebuItem(res)
                    })
                }
            </SubMenu>
        )
    }
    setMenu = () => {
        return (
            <Menu
                mode="inline"
                inlineIndent='0'
                onOpenChange={this.onOpenChange}
                onClick={this.selete}
                openKeys={this.props.appStore.openkeys.length > 0 ? this.props.appStore.openkeys.slice() : [this.props.appStore.getCompanyBuild.linked]}

            >
                <SubMenu
                    key={this.props.appStore.getCompanyBuild.linked}
                    title={
                        <div  className={this.props.appStore.seleteId==this.props.appStore.getCompanyBuild.id?'firsttDept link-active':''}>
                            <Icon type="caret-down" />

                            <img className='firstImg' style={{ 'width': '20px' }} src={this.props.appStore.seleteId==this.props.appStore.getCompanyBuild.id?require("./../../../assets/images/icon-staff-4.png"):require("./../../../assets/images/icon-staff-4-1.png")} />
                            <span>{this.props.appStore.getCompanyBuild.name}{this.getCount(this.props.appStore.getCompanyBuild.id, this.props.appStore.countDept)}</span>
                        </div>

                    }
                    onTitleClick={this.onTitleClick}
                >
                    {
                        this.props.appStore.getCompanyBuild.list && this.props.appStore.getCompanyBuild.list.map(val => {
                            return val.list ? this.setSubMenu(val) : this.setMebuItem(val)
                        })

                    }
                </SubMenu>
            </Menu>
        )
    }
    render() {

        const { getFieldDecorator } = this.props.form;
        return (
            <div id="Staff">
                <Router>
                    <div className='Slider'>
                        {/* {
                            this.state.tabKey == 2 ? <div className='addRoleBtn' onClick={this.onShowAddRole}>
                                新增角色
                </div> : ''
                        } */}

                        <div className="search">
                            <Search
                                placeholder="搜索"
                                onSearch={this.getSearch}
                                style={{ width: '91.52%' }}
                            />
                        </div>
                        <div className="tab">
                            <Tabs activeKey={this.state.tabKey.toString()} onChange={this.callback} animated={{ 'inkBar': false }}>
                                <TabPane tab={<Link to='/Staff/Dept'>组织架构</Link>} key="1">
                                    <div className="organize">
                                        {this.setMenu()}
                                    </div>
                                </TabPane>
                                <TabPane tab={<Link to='/Staff/Role'>角色</Link>} key="2">
                                    <div className='role'>
                                        <Menu
                                            onClick={this.handleClick}
                                            style={{ width: '100%' }}
                                            theme='dark'
                                            mode="inline"
                                            defaultOpenKeys={[this.props.appStore.roleId.toString()]}
                                            selectedKeys={[this.props.appStore.roleId.toString()]}
                                        >
                                            {
                                                this.props.appStore.listRole.slice().map(res => {
                                                    return (
                                                        <Menu.Item key={res.id} value={res.role}>
                                                            <div className='roleName'>{res.role}</div>
                                                        </Menu.Item>
                                                    )
                                                })
                                            }
                                        </Menu>
                                        <div className="addRoleBtn" onClick={this.onShowAddRole}>新增角色</div>
                                    </div>
                                </TabPane>

                            </Tabs>
                        </div>
                    </div>
                    {/* <div style={Styles.show}> */}
                    <Switch>
                        <Route path='/Staff/Dept' component={Dept}></Route>
                        <Route exact path='/Staff/Role' component={Role}></Route>
                    </Switch>
                    {/* </div> */}

                    <Drawer
                        title="新增角色"
                        placement="right"
                        closable={false}
                        onClose={this.onCloseAddRole}
                        visible={this.state.AddRole}
                        width='38.53%'
                    >
                        <div className="drawer">
                            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleAddDept}>
                                <div className="drawer_title">
                                    <span>基础权限</span>
                                    <div className="flex"></div>

                                </div>

                                <Form.Item label="权限名称:">
                                    {getFieldDecorator('name', {
                                        rules: [{ required: true, message: "权限名称不能为空！" }],
                                    })(<Input />)}
                                </Form.Item>
                                <div className='addRole'>
                                    <div className='head'>
                                        <span>选择权限</span>
                                        <div className="flex"></div>
                                    </div>



                                    <div className='formItem'>
                                        <Form.Item >
                                            {getFieldDecorator('checkboxGroup')(

                                                <Checkbox.Group style={{ width: '100%' }} >
                                                    {
                                                        this.state.listPermission && this.state.listPermission.map(res => {
                                                            if (res && res.list && res.list.length > 0)
                                                                return (
                                                                    <div>
                                                                        <div className="power_title">
                                                                            {
                                                                                res.type == 1 ? '一. ' + res.value : res.type == 2 ? '二. ' + res.value : res.type == 3 ? '三. ' + res.value : res.type == 1 ? '四. ' + res.value : '五。 ' + res.value
                                                                            }
                                                                        </div>
                                                                        <Row>
                                                                            {
                                                                                res.list.map(val => {
                                                                                    return (
                                                                                        <Col span={8}>
                                                                                            <Checkbox value={val.id}>{val.name}</Checkbox>
                                                                                        </Col>
                                                                                    )

                                                                                })
                                                                            }


                                                                        </Row>
                                                                    </div>
                                                                )
                                                        }

                                                        )
                                                    }
                                                </Checkbox.Group>

                                            )}
                                        </Form.Item>

                                    </div>
                                </div>
                                <div className="btn">
                                    <Button className='save' onClick={this.handleAddRole}>保存</Button>
                                    <Button className='saveRefresh' onClick={this.saveRefreshRole}>保存并继续添加</Button>
                                    <Button className='close' onClick={this.onCloseAddRole}>取消</Button>

                                </div>
                            </Form>
                        </div>

                    </Drawer>
                </Router>
            </div>
        )
    }
}
function onChange(checkedValues) {
    console.log('checked = ', checkedValues);
}

const Styles = {
    submenu: {
        'paddingLeft': '0px'
    },
    hide: {
        'display': 'none'
    },
    show: {
        'flex': '1',
        'backgroundColor': '#fff'
    }
}
const WrappedRegistrationForm = Form.create()(Index);
export default WrappedRegistrationForm